#app{font-family:-apple-system, ".SFNSText-Regular", "San Francisco", "Roboto", "Segoe UI", "Helvetica Neue", "Lucida Grande", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50;margin-top:60px
}
#types{max-width:90%;margin:auto;margin-bottom:1rem;margin-top:1rem
}
#types span.marker{display:inline-block;position:relative;z-index:1
}
#types span.marker:before{content:' ';position:absolute;top:0;left:0;height:100%;width:100%;border-radius:2px;background:#616161;z-index:0;opacity:0
}
#types span:hover:before{opacity:.2
}
#types tr th span:not(.active){background:#fff;border:1px solid #EFEFEF;color:#9e9e9e
}
#types .weak-against-types span:nth-of-type(2){background:#fff59d
}
#types .weak-against-types span:nth-of-type(3){background:#ffcc80
}
#types .weak-against-types span:nth-of-type(4){background:#ef9a9a
}
#types .weak-against-types span:nth-of-type(5){background:#ef5350
}
#types .weak-against-types span:nth-of-type(6){background:#e53935
}
#types .resists-against-types span:nth-of-type(2){background:#80deea
}
#types .resists-against-types span:nth-of-type(3){background:#81d4fa
}
#types .resists-against-types span:nth-of-type(4){background:#42a5f5
}
#types .resists-against-types span:nth-of-type(5){background:#1e88e5
}
#types .resists-against-types span:nth-of-type(6){background:#0d47a1
}
#types .immune{background:#cddc39 !important
}
#types li{display:inline-block;margin:5px
}
span.btn{display:inline-block;padding:5px 10px;border-radius:2px;margin:2px;color:rgba(33,33,33,0.6)
}
span.btn:hover{background:#616161
}
.btn-primary{background-color:#e0e0e0;border-radius:2px;border:none;text-transform:lowercase;transition:all 0.25s ease
}
.btn-primary:hover{background-color:#bdbdbd;transition:all 0.25s ease
}
.btn.btn-ghost{background-color:transparent !important;color:#bdbdbd;transition:all 0.25s ease
}
.btn.btn-ghost:hover{color:#757575;transition:all 0.25s ease
}
table.table{text-align:left;width:90%
}
table.table thead th{text-transform:uppercase;border-bottom:1px solid #eceeef;color:rgba(97,97,97,0.6)
}
table.table tbody tr:first-of-type th,table.table tbody tr:first-of-type td{padding-top:5px
}
table.table tbody tr th{padding:2px;border-bottom:transparent;border-top:transparent
}
table.table td{border-top:transparent;padding:1px
}
span.marker{background:#e0e0e0;height:30px;border:transparent;width:10px;padding:0px
}
input.form-control{border-radius:2px;border-color:#EFEFEF
}
input.form-control:focus{border-color:#cddc39
}
#pokemon{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;max-width:100%;margin:auto;margin-top:2rem
}
.pokemon{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-wrap:nowrap;flex-wrap:nowrap;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-ms-flex-line-pack:center;align-content:center;margin:.5rem
}
.pokemon .sprite-container{width:50px;height:50px;overflow:hidden;border-radius:100%;padding:4px;border:2px solid #e0e0e0;background:#EFEFEF
}
.pokemon .sprite-container:hover{border-color:#f06292;cursor:pointer
}
.pokemon .sprite{object-fit:contain;width:100%;height:100%
}
ul.team-members{background:#EFEFEF;border-radius:4px;display:block;max-width:90%;margin:auto;padding:20px
}
ul.team-members li{max-width:14%
}
ul.team-members li div.sprite-container{display:block;position:relative
}
ul.team-members li div.sprite-container:before{font-family:fontawesome;content:'\f00d';position:absolute;top:0;left:0;opacity:0;transition:opacity 0.5s ease;color:#e57373
}
ul.team-members li div.sprite-container:hover:before{opacity:1;transition:opacity 0.5s ease
}
ul.team-members li img{object-fit:contain;width:100%;height:100%
}
ul.team-members li.empty-text{max-width:100%
}
.animated-wrapper{max-height:100%;transition:all 0.5s ease
}
.closed{max-height:100px;transition:all 0.25s ease;overflow:hidden
}
.hidden{opacity:0;transition:opacity .5s ease
}
table.table{transition:opacity .25s ease
}
.list-complete-item{transition:all .5s;display:inline-block;margin-right:10px
}
.list-complete-enter,.list-complete-leave-active{opacity:0;transform:translateY(10px)
}
.list-complete-leave-active{position:absolute
}
ul{list-style-type:none;padding:0
}
li{display:inline-block;margin:0 10px
}
a{color:#42b983
}
.special{text-transform:uppercase;font-weight:bold;letter-spacing:3px;font-size:1.2rem;color:rgba(0,0,0,0.3);margin-top:1rem
}
.search-box{width:90%;max-width:400px;margin:auto
}
#pokebank{border:1px solid #EFEFEF;max-width:90%;margin:auto;border-radius:4px;margin-bottom:4rem
}
#toggle-button{width:50px;padding:.75rem 0rem .75rem 0rem
}
#toggle-button a.btn{padding:.5rem .75rem
}



